import React from 'react'
import {Card, Cascader, Tooltip, Icon, Form, Checkbox, Select, Input, Button, Col, Row, message, BackTop, Spin} from 'antd'
import CustomBreadcrumb from '../../components/CustomBreadcrumb/index'
import TypingCard from '../../components/TypingCard'
import {HOST, ADD_SERVER, TEST, HOST_TEST} from '../../utils/url_config'
import {_fetch, match_obj} from "../../utils/utils"

const FormItem = Form.Item
const Option = Select.Option


@Form.create()
class Server_add extends React.Component {
    state = {
        text: '',
        disabled: false,
        loading: false,
    }
    timer = 0
    countdown = (e) => {
        let time = 60
        this.setState({
            text: --time + 's',
            disabled: true
        })
        this.timer = setInterval(() => {
            if (time > 0) {
                this.setState({
                    text: --time + 's',
                    disabled: true
                })
            } else {
                this.setState({
                    text: '',
                    disabled: false
                })
            }
        }, 1000)
    }
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (err) {
                message.warning('')
            } else {
                let query_url = HOST() + ADD_SERVER
                let { name, interval, user, pwd, host } = values
                let new_data = {
                    name,
                    interval,
                    user,
                    pwd,
                    host,
                }
                this.setState({
                    loading: true
                },()=>{
                    _fetch(query_url,{
                        ...new_data
                    },(json)=>{
                        this.setState({
                            loading: false
                        },()=>{
                            // console.log(json)
                            if (json.status === 200){
                                message.success('')
                            }
                            else {
                                message.error(json.err_msg)
                            }
                        })
                    })
                })
                // _fetch(query_url,{
                //     ...new_data
                // },(json)=>{
                //     console.log(json)
                //     if (json.status === 200){
                //         message.success('')
                //     }
                //     else {
                //         message.error(json.err_msg)
                //     }
                // })
            }
        });
    }

    componentWillUnmount() {
        clearInterval(this.timer)
    }

    componentWillMount() {
        // let { name, interval, user, pwd, host } = this.props.server_info
        // this.show_info = {
        //     name,
        //     interval,
        //     user,
        //     pwd,
        //     host,
        //     confirm: pwd
        // }
    }

    render() {

        const {getFieldDecorator, getFieldValue} = this.props.form
        const formItemLayout = {
            labelCol: {
                xs: {span: 24},
                sm: {span: 4},
            },
            wrapperCol: {
                xs: {span: 24},
                sm: {span: 12},
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 12,
                    offset: 4,
                },
            },
        }
        const prefixSelector = getFieldDecorator('prefix', {
            initialValue: 86,
        })(
            <Select style={{width: 70}}>
                <Option value={86}>+86</Option>
                <Option value={87}>+87</Option>
            </Select>
        );
        return (
            <div >
                <CustomBreadcrumb arr={[{title:'', to:'/server_info'}, '']}/>
                <Spin spinning={this.state.loading}>
                <Card bordered={false} title=''>
                    <Form layout='horizontal' style={{width: '70%', margin: '0 auto'}} onSubmit={this.handleSubmit}>
                        <FormItem {...formItemLayout} required label={(
                            <span>
                &nbsp;
                                <Tooltip title=' :  80'>
                                    <Icon type='question-circle-o'/>
                                </Tooltip>
                            </span>
                        )}>
                            {
                                getFieldDecorator('name', {
                                    rules: [
                                        {
                                            required: true,
                                            message: ''
                                        }
                                    ],
                                })(
                                    <Input/>
                                )
                            }
                        </FormItem>
                        <FormItem label='' {...formItemLayout}>
                            {
                                getFieldDecorator('user', {
                                    rules: [
                                        // {
                                        //     whitespace: true,
                                        //     message: ''
                                        // },
                                        {
                                            pattern: /^[a-zA-Z0-9]+$/,
                                            message: ''
                                        },
                                        {
                                            required: true,
                                            message: ''
                                        }
                                    ],
                                })(
                                    <Input/>
                                )
                            }
                        </FormItem>
                        <FormItem label='' {...formItemLayout}>
                            {
                                getFieldDecorator('pwd', {
                                    rules: [
                                        {
                                            required: true,
                                            message: ''
                                        },
                                        {
                                            min: 6,
                                            message: '6'
                                        },
                                        {
                                            max: 16,
                                            message: '16'
                                        },
                                        {
                                            whitespace: true,
                                            message: ''
                                        }
                                    ],
                                })(
                                    <Input type='password'/>
                                )
                            }
                        </FormItem>
                        <FormItem label='' {...formItemLayout} required>
                            {
                                getFieldDecorator('confirm', {
                                    rules: [
                                        {
                                            validator: (rule, value, callback) => {
                                                const {getFieldValue} = this.props.form
                                                if (!getFieldValue('pwd')) {
                                                    callback('')
                                                }
                                                if (value && value !== getFieldValue('pwd')) {
                                                    callback('')
                                                }
                                                callback()
                                            }
                                        },
                                        {
                                            required: true,
                                            message: ''
                                        },
                                    ]
                                })(
                                    <Input type='password'/>
                                )
                            }
                        </FormItem>
                        <FormItem label='ip ' {...formItemLayout} required>
                            {
                                getFieldDecorator('host', {
                                    rules: [
                                        {
                                            pattern:/^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/,
                                            required: true,
                                            message: ' ip '
                                        }
                                    ],
                                })(
                                    <Input />
                                )
                            }
                        </FormItem>
                        <FormItem label='' {...formItemLayout} required>
                            {
                                getFieldDecorator('interval', {
                                    rules: [
                                    ],
                                })(
                                    <Select>
                                        <Option value={10*60}>10 </Option>
                                        <Option value={30*60}>30 </Option>
                                        <Option value={60*60}>60 </Option>
                                    </Select>
                                )
                            }
                        </FormItem>
                        <FormItem {...tailFormItemLayout}>
                            {getFieldDecorator('agreement', {
                                valuePropName: 'checked',
                            })(
                                <Checkbox><a></a></Checkbox>
                            )}
                        </FormItem>
                        <FormItem style={{textAlign: 'center'}} {...tailFormItemLayout}>
                            <Button type="primary" htmlType="submit" disabled={!getFieldValue('agreement')}></Button>
                        </FormItem>
                    </Form>
                </Card>
                <BackTop visibilityHeight={200} style={{right: 50}}/>
                </Spin>
            </div>
        )
    }
}

export default Server_add


